import httpserver from "../../utils/http.js";
import api from "../../utils/api.js";
export default {
  //   created() {
  //     this.saveProductionRecordAndProductionAssembies();
  //   },
  data() {
    return {
      scanCode: "",
      serialNO: "",
      serialNo2: "",
      workStationCode: "",
      data: []
    };
  },
  methods: {
    saveProductionRecordAndProductionAssembies() {
       let loc = JSON.parse(window.localStorage.getItem("terminal"));
      let body = {
        scanCode: this.scanCode,
        serialNo: this.serialNO,
        workStationCode: loc.workStationCode
      };
      httpserver(api.saveProductionRecordAndProductionAssembies, body).then(
        res => {
          if (res.data.returnCode == "0") {
            this.data.push(body);
          }
        }
      );
    },
    savePassPoint(){
      let body ={
        serialNo:this.serialNo2,
        workStationCode:this.workStationCode
      };
      httpserver(api.savePassPoint,body).then(res=>{
        if(res.data.returnCode =="0"){
          this.$message({
            type:"success",
            message:res.data.data
          })
        }
      })
    }
  }
};



