import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/views/login/login.vue'
import Index from '@/views/index/index.vue';
import SystemConfiguration from '@/views/system/systemconfiguration.vue';
import Initialize from '@/views/initialize/initialize.vue';
import AssemblyOnline from '@/views/online/assemblyOnline.vue';
import MachineplusOnline from '@/views/online/machineplusOnline.vue';
import cyheadOnline from '@/views/online/cyheadOnline.vue';
import AssembleInsert from '@/views/assembleInsert/assembleInsert.vue';
import insideAssemble from '@/views/insideAssemble/insideAssemble.vue';
import HostTest from '@/views/hottest/hottest.vue';
import MachineInsert from '@/views/machineWorkInsert/machineWorkInsert.vue';
import crankshaftOnline from '@/views/online/crankshaftOnline.vue';
import machineRepair from '@/views/machineRepair/machineRepair.vue'
import AuxMachine from '@/views/auxMachine/auxMachine.vue'
// import Message from '@/views/index/message.vue';
// import concentratRepair from '@/views/concentratRepair/concentratRepair.vue'
// import stationRepair from '@/views/stationRepair/stationRepair.vue'
import menu from '@/views/menu/menu.vue'
import temp from '@/views/temp/temp.vue'
import NotFound from '@/views/NotFound/NotFound.vue'
Vue.use(Router)


let routes = [
  {
    path: '/', redirect: '/login',
  },
  {
    path: '/', component: Index,
    children: [
      { path: '/system', component: SystemConfiguration, name: '' },
      { path: '/initialize', component: Initialize, name: '' },
      { path: '/initialize', component: Initialize, name: ''},
      { path: '/online/assemblyOnline', component: AssemblyOnline, name: '' },
      { path: '/online/machineplusOnline', component: MachineplusOnline, name: '' },
      { path: '/assembleInsert', component: AssembleInsert, name: '' },
      { path: '/insideAssemble', component: insideAssemble, name: '' },
      { path: '/hothost', component: HostTest, name: '' },
      { path: '/MachineInsert', component: MachineInsert, name: '' },
      { path: '/online/crankshaftOnline', component: crankshaftOnline, name: '' },
      { path: '/machineRepair', component: machineRepair, name: '' },
      // { path: '/concentratRepair', component: concentratRepair, name: '' },
      { path: '/auxMachine', component: AuxMachine, name: '' },
      { path: '/cyheadOnline', component: cyheadOnline, name: '' },
      { path: '/temp', component: temp, name: '' },
      { path: '/menu', component: menu, name: '' },
      { path: '/NotFound', component: NotFound, name: '404' },
    ]
  },
  { path: '/login', name: 'login', component: Login },
]



const router= new Router({
  routes: routes
})
router.beforeEach((to, from, next) => {
  
  console.log(to);
  console.log(11111);
  console.log(from);
  if (to.matched.length ===0) {                                        //
    next('/NotFound');   //
  } else {
    next();                                                                            //
  }
});

export default router


