
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'

import httpserver from "../../utils/http.js";
import api from "../../utils/api.js";
import getNowFormatDate from "../../utils/timeFormat.js";
export default {
data() {
return {
code: "", //
productionOrderNum: "",
materialCode: "",
productOrderNum: "",
materialText: "",
productModel: "",
repairDialogVisible: false, //
scrapDialogVisible: false, //
scrapType: "0",
defectType: "", //
defectTypeArr: [], //
defect: "", //
defectArr: [], //
attribute: "", //
attributeArr: [], //
repairMethod: "",
repairMethodArr: [],
orderTypeCode: "", //
memo: "", //
remark: "", //
tableData: [], //text
defectList: [], //code
dialogTableVisible: false, //
historyData: [], //
pagesize: 8, //
total: 0, //
currentpage: 1
};
},
beforeRouteLeave(to, from, next) {
this.closeCom();
next();
},
mounted() {
this.openCom();
},
components: {
Checker,
CheckerItem,
XButton,
XInput,
Group,
XDialog
},
methods: {

show: function(ev) {
let _this = this;
if (ev.keyCode == 13) {
this.getSerialNoInformation();
}
},
openCom() {
try {
let _this = this;
let baudRate = JSON.parse(window.localStorage.getItem("serialPort"))
.baudRate;
console.log(baudRate);
let port = new SerialPort(
JSON.parse(window.localStorage.getItem("serialPort")).port, {
baudRate: parseInt(baudRate),
autoOpen: false
},
false
);
console.log(port);
let Readline = SerialPort.parsers.Readline;
let parser = new Readline();
port.pipe(parser);
port.open(function(error) {
if (error) {
console.log(error);
_this.$message({
message: "",
type: "error"
});
return console.log("Error opening port:", error);
} else {
_this.$message({
message: "",
type: "success"
});
}
});
parser.on("data", function(data) {
if (data.length <= 2) {
_this.$message({
type: "error",
message: "!"
});
} else {
_this.code = data;
_this.getSerialNoInformation();
}
});
_this.serialPort = port;
} catch (err) {

}
},
closeCom() {
try {
if (this.serialPort.isOpen) {
let _this = this;
_this.serialPort.close(function(err) {
if (err) {
console.log(err);
_this.$message({
message: "",
type: "error"
});
} else {
_this.$message({
message: "",
type: "success"
});
}
});
}
} catch (err) {

}
},

getSerialNoInformation() {
if (this.code == "") {
this.$message({
type: "error",
message: ""
});
} else {
let body = {
serialNo: this.code
};
httpserver(api.getSerialNoInformation, body).then(res => {
console.log(res);
if (res.data.returnCode == "0") {
this.$message({
type: "success",
message: ""
});
this.productionOrderNum = res.data.data.productionOrderNum;
this.materialCode = res.data.data.materialCode;
this.productOrderNum = res.data.data.productOrderNum;
this.materialText = res.data.data.materialText;
this.productModel = res.data.data.productModel;
this.orderTypeCode = res.data.data.orderTypeCode;
}
});
}
},

getrepair() {
if (this.code != "" && this.materialCode != "") {
this.repairDialogVisible = true;
this.defectType = "";
this.defect = "";
this.attribute = "";
this.memo = "";
this.tableData = [];
this.getdefectType();
this.getDefectAttribute();

} else {
this.$message({
type: "warning",
message: ""
});
}
},

getdefectType() {
let body = {
workCenterCode: JSON.parse(window.localStorage.getItem("terminal"))
.workCenterCode
};
httpserver(api.getDefectType, body).then(res => {
this.defectTypeArr = res.data.data;
});
},

getDefect() {

this.defect = "";
this.defectArr = [];
if (this.defectType != "") {
let body = {
defectTypeCode: this.defectType.defectTypeCode,
workCenterCode: JSON.parse(window.localStorage.getItem("terminal"))
.workCenterCode
};
httpserver(api.getDefect, body).then(res => {
console.log(res);
if (res.data.returnCode == "0") {
this.defectArr = res.data.data;






} else {
this.defect = "";
this.defectArr = [];
}
});
} else {

this.defectArr = [];
this.defect = "";
}
},

getDefectAttribute() {
let body = {
workCenterCode: JSON.parse(window.localStorage.getItem("terminal"))
.workCenterCode
};
httpserver(api.getDefectAttribute, body).then(res => {
console.log(res.data.data);
if (res.data.returnCode == "0") {
this.attributeArr = res.data.data;
}






});
},

pushData() {
if (this.defectType == "" || this.defect == "" || this.attribute == "") {
this.$message({
type: "warning",
message: ""
});
} else {
let dataText = {
defectTypeText: this.defectType.defectTypeText,
defectText: this.defect.defectText,
attributeText: this.attribute.attributeText,
remark: this.memo
};
let dataCode = {
defectTypeCode: this.defectType.defectTypeCode,
defectCode: this.defect.defectCode,
attributeCode: this.attribute.attributeCode,
remark: this.memo
};
let temp = JSON.stringify(this.tableData).indexOf(
JSON.stringify(dataText)
);
if (temp < 0) {
this.defectList.push(dataCode);
this.tableData.push(dataText);
} else {
this.$message({
type: "warning",
message: ""
});
}
}
},

deleteRow(index, rows) {
rows.splice(index, 1);
this.defectList.splice(index, 1);
},

productionRepair() {
let loc = JSON.parse(window.localStorage.getItem("terminal"));
let body = {
workStationCode: loc.workStationCode,
productionOrderNum: this.productionOrderNum,
serialNo: this.code,
orderTypeCode: this.orderTypeCode,
materialCode: this.materialCode,
defectList: this.defectList
};
httpserver(api.productionRepair, body).then(res => {
console.log(res);
if (res.data.returnCode == "0") {
this.$message({
type: "success",
message: res.data.data
});

this.repairDialogVisible = false;
this.empty();
}
});
},

openscrapDialog() {
if (this.code != "" && this.materialCode != "") {
this.scrapDialogVisible = true;
} else {
this.$message({
type: "warning",
message: ""
});
}
},

scrap() {
let loc = JSON.parse(window.localStorage.getItem("terminal"));
let body = {
productionOrderNum: this.productionOrderNum,
workStationCode: loc.workStationCode,
serialNo: this.code,
materialCode: this.materialCode,
isScrap: 1,
scrapType: this.scrapType,
remark: this.remark,
orderTypeCode: this.orderTypeCode
};
httpserver(api.scrap, body).then(res => {
if (res.data.returnCode == "0") {
this.$message({
type: "success",
message: res.data.data
});

this.scrapDialogVisible = false;

this.empty();
}
});
},

misJud() {
if (this.code != "" && this.materialCode != "") {
let loc = JSON.parse(window.localStorage.getItem("terminal"));
let body = {
productionOrderNum: this.productionOrderNum,
workStationCode: loc.workStationCode,
serialNo: this.code,
materialCode: this.materialCode,
isScrap: 0,
misJud: 1,
orderTypeCode: this.orderTypeCode
};
httpserver(api.misJud, body).then(res => {
if (res.data.returnCode == "0") {
this.$message({
type: "success",
message: res.data.data
});


this.empty();
}
});
} else {
this.$message({
type: "warning",
message: ""
});
}
},

empty() {
this.code = "";
this.productionOrderNum = "";
this.productOrderNum = "";
this.materialCode = "";
this.materialText = "";
this.productModel = "";
this.tableData = [];
this.defectList = [];
this.defectType = "";
this.defect = "";
this.attribute = "";
},


acceptDeviation() {
if (this.code != "" && this.materialCode != "") {
let body = {
serialNo: this.code,
materialCode: this.materialCode,
materialText: this.materialText
};
httpserver(api.acceptDeviation, body).then(res => {
console.log(res);
if (res.data.returnCode == "0") {
this.$message({
type: "success",
message: res.data.data
});

this.empty();
}
});
} else {
this.$message({
type: "warning",
message: ""
});
}
},


getRepairRecords() {
this.dialogTableVisible = true;
let loc = JSON.parse(window.localStorage.getItem("terminal"));

let body = {
pageNo: this.currentpage,
pageSize: this.pagesize,
workStationCode: loc.workStationCode
};
httpserver(api.getRepairRecords, body).then(res => {
console.log(res);
this.historyData = res.data.data.list;
this.total = res.data.data.toalCount;
});
},

dateFormat: function(row, column) {
var date = row[column.property];
if (date == undefined) {
return "";
}
return getNowFormatDate.getNowFormatDate(date);
},

currentChange(val) {
let loc = JSON.parse(window.localStorage.getItem("terminal"));
this.currentpage = val;
let body = {
workStationCode: loc.workStationCode,
pageNo: this.currentpage,
pageSize: this.pagesize
};
httpserver(api.getRepairRecords, body).then(response => {
this.historyData = response.data.data.list;
this.total = response.data.data.toalCount;
});
}
}
};




