import getTime from "@/utils/timeFormat";
import timer from "@/utils/timerManager";
import httpserver from "../../utils/http.js";
import api from "../../utils/api.js";
import mqttLib from "../../utils/mqtt.js";

export default {
  data() {
    return {
      time1: "",
      time2: "",
      fullscreen: true,
      messages: "",
      num: 0,
      messageDialogVisible: false,
      sysdate: "",
      week: "",
      systime: "",
      isMenuShow: false,
      wifiStatus: "0",
      tabPosition: "top",
      activeNames: ["1"],
      chooseTypeVal: 2,
      isActive1: true,
      isActive2: false,
      isActive3: false,
      isHasRead: false,
      animate: false,
      listData: [
        {
          title: "     ,  !   "
        }
      ],
      responseTimeMs: -1
    };
  },
  computed: {
    classOption() {
      return {
        step: 0.5, //the faster the rolling speed is faster
        limitMoveNum: 0, //start seamless scrolling minimum data  //this.dataList.length
        hoverStop: false, //mouse hover control is enabled
        direction: 2 // 0 down || 1 up || 2 left || 3 right
      };
    }
  },
  created() {
    let body = document.querySelector("body");
    body.addEventListener(
      "click",
      e => {
        if (e.target.id !== "ment-list") {
          this.isMenuShow = false;
        }
      },
      false
    );
    this.getServertime();
    this.detectNetwork();

    // this.gettest();
  },
  beforeDestroy() {
    this.clear();
  },
  mounted: function() {},
  methods: {
    getMenu() {
      this.$router.push("/menu");
    },
    //      
    requestFullScreen() {
      var element = document.documentElement;
      if (this.fullscreen == true) {
        if (element.requestFullscreen) {
          element.requestFullscreen();
        } else if (element.mozRequestFullScreen) {
          element.mozRequestFullScreen();
        } else if (element.webkitRequestFullscreen) {
          element.webkitRequestFullscreen();
        } else if (element.msRequestFullscreen) {
          element.msRequestFullscreen();
        }
        this.fullscreen = !this.fullscreen;
      } else {
        if (document.exitFullscreen) {
          document.exitFullscreen();
        } else if (document.mozCancelFullScreen) {
          document.mozCancelFullScreen();
        } else if (document.webkitExitFullscreen) {
          document.webkitExitFullscreen();
        }
        this.fullscreen = !this.fullscreen;
      }
      this.isActive1 = false;
      this.isActive2 = false;
      this.isActive3 = true;
    },
    //      
    menuClick() {
      this.isMenuShow = !this.isMenuShow;
      this.isActive1 = true;
      this.isActive2 = false;
      this.isActive3 = false;
    },
    getMessage() {
      this.messageDialogVisible = true;
      this.isActive1 = false;
      this.isActive2 = true;
      this.isActive3 = false;
    },
    //      +
    getServertime() {
      let _this = this;
      let sendDate = new Date().getTime();
      httpserver(api.getServertime).then(response => {
        let receiveDate = new Date().getTime();
        _this.responseTimeMs = receiveDate - sendDate;
        _this.wifiStrong();
        let data = new Date(response.data.data).getTime(); //
        const date = getTime.gettime(data);
        _this.sysdate = getTime.five(date);
        _this.week = date.week;
        _this.time1 = window.setInterval(function() {
          data = data + 1000;
          _this.systime = getTime.six(data);
        }, 1000);
      })
       .catch(err => {
            if (err) {
              console.log(111);
            }
          });
    },
    //
    detectNetwork() {
      let _this = this;
      this.time2 = window.setInterval(function() {
        let sendDate = new Date().getTime();
        httpserver(api.getServertime)
          .then(response => {
            //
            let receiveDate = new Date().getTime();
            _this.responseTimeMs = receiveDate - sendDate;
            _this.wifiStrong();
            let data = new Date(response.data.data).getTime(); //
            const date = getTime.gettime(data);
            _this.sysdate = getTime.five(date);
            _this.week = date.week;
          })
          .catch(err => {
            if (err) {
            }
          });
      }, 20000);
    },
    wifiStrong() {
      if (this.responseTimeMs >= 0 && this.responseTimeMs < 50) {
        return "strong5";
      } else if (this.responseTimeMs >= 50 && this.responseTimeMs < 200) {
        return "strong4";
      } else if (this.responseTimeMs >= 200 && this.responseTimeMs < 500) {
        return "strong3";
      } else if (this.responseTimeMs >= 500 && this.responseTimeMs < 1000) {
        return "strong2";
      } else if (this.responseTimeMs >= 1000 && this.responseTimeMs < 1900) {
        return "strong1";
      } else {
        return "strongnone";
      }
    },
    //
    clear() {
      clearInterval(this.time1);
      clearInterval(this.time2);
    },
    //
    subscribe() {
      let _this = this;
      let topic = "/logs";
      console.log("begin----------");
      mqttLib.subscribe(topic, "message");
      mqttLib.registerMessageHandler(topic, "message", function(message) {
        _this.messages = message.payloadString;
        console.log(_this.messages);
      });
    },
    //
    unsubscribe() {
      console.log("close----------");
      mqttLib.unsubscribe("/message", "message");
    },
    handleChange(val) {
      let order = val[val.length - 1];
      this.messageData[order].status = 1;
    },
    haveRead() {
      this.isHasRead = true;
    },
    switchPane(e) {
      this.chooseTypeVal = e === "" ? 2 : e === "" ? 1 : 0;
    },
    messlist2: function(list) {
      return this.messageData.filter(function(item) {
        return item.status == 2;
      });
    },
    messlist1: function(list) {
      return this.messageData.filter(function(item) {
        return item.status == 1;
      });
    },
    messlist0: function(list) {
      return this.messageData.filter(function(item) {
        return item.status == 0;
      });
    },
    //    
    changeColor() {}
  }
};



