import httpserver from "../../utils/http.js";
import api from "../../utils/api.js";
import mqttLib from "../../utils/mqtt.js";
import getNowFormatDate from "../../utils/timeFormat.js";

export default {
  data() {
    return {
      currentPage: 1, //
      pagesize: 6,
      tableData: [],
      proinfo: {},
      serialNo: "",
      dialogTableVisible: false,
      gridData: [],
      number: 0,
      total: 0,
      StepDatas: [
        {
          title: ",...",
          status: "wait"
        },
        {
          title: "",
          status: "wait"
        },
        {
          title: "TAG...",
          status: "wait"
        },
        {
          title: "",
          status: "wait"
        },
        {
          title: "",
          status: "wait"
        },
        {
          title: "",
          status: "wait"
        }
      ]
    };
  },
  created() {
    this.ptqueues();
    this.subscribe();
  },
  beforeDestroy: function() {
    this.unsubscribe();
  },
  methods: {
    getHistoryInfo() {
      this.dialogTableVisible = true;
      let loc = JSON.parse(window.localStorage.getItem("terminal"));
      let body = {
        workCenterCode: loc.workCenterCode,
        pageNo: this.currentPage,
        pageSize: this.pagesize
      };
      httpserver(api.ptHistorys, body).then(response => {
        if (response.data.returnCode == "0") {
          let resData = response.data.data;
          this.gridData = resData.ptHistory;
          this.total = resData.toalCount;
        }
      });
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      let loc = JSON.parse(window.localStorage.getItem("terminal"));
      let body = {
        workCenterCode: loc.workCenterCode,
        pageNo: this.currentPage,
        pageSize: this.pagesize
      };
      httpserver(api.ptHistorys, body).then(response => {
        console.log(333);
        let resData = response.data.data;
        this.gridData = resData.ptHistory;
        this.total = resData.toalCount;
      });
    },
    dateFormat: function(row, column) {
      var date = row[column.property];
      if (date == undefined) {
        return "";
      }
      return getNowFormatDate.getNowFormatDate(date);
    },
    ptqueues() {
      let loc = JSON.parse(window.localStorage.getItem("terminal"));
      let body = {
        workCenterCode: loc.workCenterCode,
        
      };
      httpserver(api.ptqueues, body).then(res => {
        this.tableData = res.data.data;
      });
    },
    subscribe() {
      console.log("");
      let _this = this;
      let loc = JSON.parse(window.localStorage.getItem("terminal"));
      let workStationCode = loc.workStationCode;
      // let topic = "/logs/B01";
      let topic = "/logs/" + workStationCode;
      mqttLib.subscribe(topic, "message");
      mqttLib.registerMessageHandler(topic, "message", function(message) {
        let data = JSON.parse(message.payloadString);
        console.log(data);
        let content = data.Content;
        let type = data.Type;
        if (type == "LOAD") {
          let step = content.Step;
          let log = content.Log;
          console.log(step);
          switch (step) {
            case "1":
              _this.number = 1;
              _this.StepDatas.forEach(function(value, index, array) {
                array[index].status = "wait";
              });
              _this.StepDatas[0].status = "success";
              _this.StepDatas[0].title = log;
              break;
            case "2":
              _this.number = 2;
              _this.StepDatas[1].status = "success";
              _this.StepDatas[1].title = log;
              break;
            case "3":
              _this.number = 3;
              _this.StepDatas[2].status = "success";
              _this.StepDatas[2].title = log;
              break;
            case "4":
              _this.number = 4;
              _this.StepDatas[3].status = "success";
              _this.StepDatas[3].title = log;
              break;
            case "5":
              _this.number = 5;
              _this.StepDatas[4].status = "success";
              _this.StepDatas[4].title = log;
              break;
            case "6":
              _this.number = 6;
              _this.StepDatas[5].status = "success";
              _this.StepDatas[5].title = log;
              _this.ptqueues();
              break;
            default:
              _this.StepDatas[_this.number].status = "error";
              _this.StepDatas[_this.number].title = log;
          }
        } else {
          _this.serialNo = content.Log;
          let body = {
            serialNo: _this.serialNo
          };
          httpserver(api.getSerialNoInformation, body).then(res => {
            if (res.data.returnCode == "0") {
              _this.proinfo = res.data.data;
            }
          });
        }
      });
    },
    unsubscribe() {
      let loc = JSON.parse(window.localStorage.getItem("terminal"));
      let workStationCode = loc.workStationCode;
      // let topic = "/logs/B01";
      let topic = "/logs/" + workStationCode;
      mqttLib.unsubscribe(topic, "message");
    }
  }
};



