import httpserver from "../../utils/http.js";
import api from "../../utils/api.js";
import particles from "../../js/vendors1/particles/particles";
import Config from "../../config/config.js";
import Mytitle from "../../components/mytitle/Mytitle";
export default {
  components: {
    Mytitle
  },
  data() {
    return {
      // form: {
      //   userCode: "",
      //   password: "",
      //   siteCode: "2018"
      // },
      userCode: "",
      password: "",
      // siteCode: Config.siteCode,
      siteCode: "",
      value: true,
      MenuListData: [],
      rule: {
        nameRule: [{ required: true, message: "" }],
        passwordRule: [{ required: true, message: "" }]
      },
      loadingFlag: false
    };
  },
  mounted() {
    this.getCookie();
    particlesJS("particles-js", {
      particles: {
        number: {
          value: 80,
          density: {
            enable: true,
            value_area: 800
          }
        },
        color: {
          value: "#ffffff"
        },
        shape: {
          type: "circle",
          stroke: {
            width: 0,
            color: "#000000"
          },
          polygon: {
            nb_sides: 5
          },
          image: {
            src: "img/github.svg",
            width: 100,
            height: 100
          }
        },
        opacity: {
          value: 0.5,
          random: false,
          anim: {
            enable: false,
            speed: 1,
            opacity_min: 0.1,
            sync: false
          }
        },
        size: {
          value: 5,
          random: true,
          anim: {
            enable: false,
            speed: 40,
            size_min: 0.1,
            sync: false
          }
        },
        line_linked: {
          enable: true,
          distance: 150,
          color: "#ffffff",
          opacity: 0.4,
          width: 1
        },
        move: {
          enable: true,
          speed: 6,
          direction: "none",
          random: false,
          straight: false,
          out_mode: "out",
          attract: {
            enable: false,
            rotateX: 600,
            rotateY: 1200
          }
        }
      },
      interactivity: {
        detect_on: "canvas",
        events: {
          onhover: {
            enable: true,
            mode: "repulse"
          },
          onclick: {
            enable: true,
            mode: "push"
          },
          resize: true
        },
        modes: {
          grab: {
            distance: 400,
            line_linked: {
              opacity: 1
            }
          },
          bubble: {
            distance: 400,
            size: 40,
            duration: 2,
            opacity: 8,
            speed: 3
          },
          repulse: {
            distance: 200
          },
          push: {
            particles_nb: 4
          },
          remove: {
            particles_nb: 2
          }
        }
      },
      retina_detect: true,
      config_demo: {
        hide_card: false,
        background_color: "#b61924",
        background_image: "",
        background_position: "50% 50%",
        background_repeat: "no-repeat",
        background_size: "cover"
      }
    });
  },
  created() {},
  methods: {
    onSubmit() {
      let _this = this;
      let body = {
        userCode: _this.userCode,
        password: _this.password,
        siteCode: _this.siteCode
      };
      httpserver(api.logIn, body).then(response => {
        // if (_this.value == true) {
        //   this.setCookie(_this.userCode, _this.password, 7);
        // } else {
        //   this.clearCookie();
        // }
        if (response.data.returnCode == "0") {
          window.localStorage.setItem("userCode", _this.userCode);
          let menulist = JSON.stringify(response.data.data.menus);
          window.localStorage.setItem("list", menulist);
          window.localStorage.setItem(
            "sessionId",
            response.data.data.sessionId
          );
          window.localStorage.setItem(
            "terminal",
            JSON.stringify(response.data.data.terminal)
          );
          window.localStorage.setItem(
            "serialPort",
            JSON.stringify(response.data.data.serialPort)
          );
          // let serialPort = response.data.data.serialPort;
          // if (
          //   serialPort == null ||
          //   serialPort == undefined ||
          //   serialPort == ""
          // ) {
          //   console.log(serialPort);
          //   this.$router.push("/system");
          // }
          let homepage = response.data.data.terminal.homePage;
          if (homepage == null || homepage == undefined || homepage == "") {
            this.$message({
              message: "",
              type: "warning"
            });
            this.$router.push("/menu");
          } else {
            this.$router.push(response.data.data.terminal.homePage);
          }
        }
      });
    },

    setCookie(c_name, c_pwd, exdays) {
      let exdate = new Date(); //
      exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * exdays); //
      console.log(exdate.toGMTString());
      //cookie
      window.document.cookie =
        "userCode" + "=" + c_name + ";path=/;expires=" + exdate.toGMTString();
      window.document.cookie =
        "password" + "=" + c_pwd + ";path=/;expires=" + exdate.toGMTString();
    },
    getCookie: function() {
      console.log(document.cookie.length);
      if (document.cookie.length > 0) {
        this.value = true;
        console.log(document.cookie);
        let arr = document.cookie.split("; "); //
        for (var i = 0; i < arr.length; i++) {
          let arr2 = arr[i].split("="); //
          //
          if (arr2[0] == "userCode") {
            this.userCode = arr2[1]; //
          } else if (arr2[0] == "password") {
            this.password = arr2[1];
          }
        }
      } else {
        this.value = false;
      }
    },
    clearCookie: function() {
      this.setCookie("", "", -1); //21
    }
  }
};



