import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex)
//state
const state = {
    station: '',
    titles:'',
}

// actions ,,mutaions
const  actions = {
    increment:({commit})=>commit('increment'),
}

//
const mutations={
    increment (state){
        state.station="aaaa"
    },
    titles(state,val){
      console.log(val)
      state.titles=val
      
    }
}
export default new Vuex.Store({
    state,
    mutations,
    actions
})



