function gettime(t) {
    var date = new Date();
    if (t) {
        date = new Date(t)
    };
    var obj = {};
    obj.year = date.getFullYear();
    obj.month = change(date.getMonth() + 1);
    obj.day = change(date.getDate());
    obj.hour = change(date.getHours());
    obj.minute = change(date.getMinutes());
    obj.second = change(date.getSeconds());
    obj.week = changeweek(date.getDay())
    return obj;
}

function change(l) {
    return l.toString().length < 2 ? '0' + l : l;
}

function changeweek(week) {
    let str = '';
    switch (week) {  
        case 0 :  
                str += "";  
                break;  
        case 1 :  
                str += "";  
                break;  
        case 2 :  
                str += "";  
                break;  
        case 3 :  
                str += "";  
                break;  
        case 4 :  
                str += "";  
                break;  
        case 5 :  
                str += "";  
                break;  
        case 6 :  
                str += "";  
                break;  
    }
    return str
}

function changeTimeLength(t = 0){
    if(typeof t != 'number'){
        t = String(t);
    }
    const hour = Math.floor(t/3600);
    const min  = Math.floor((t%3600)/60);
    return `${hour}${min}`
}
function one(t) {
    if(!t){
        return ''
    }
    var time = typeIsObj(t) ? t : gettime(t);
    return time.year + '-' + time.month + '-' + time.day;
};

function two(t) {
    if(!t){
        return ''
    }
    var time = typeIsObj(t) ? t : gettime(t);
    return time.year + '-' + time.month + '-' + time.day + ' ' + time.hour + 'h';
};

function three(t) {
    if(!t){
        return ''
    }
    var time = typeIsObj(t) ? t : gettime(t);
    return time.year + '-' + time.month + '-' + time.day + ' ' + time.hour + ':' + time.minute;
};

function four(t) {
    if(!t){
        return ''
    }
    var time = typeIsObj(t) ? t : gettime(t);
    return time.year + '-' + time.month + '-' + time.day + ' ' + time.hour + ':' + time.minute + ':' + time.second;
}

function five(t) {
    if(!t){
        return ''
    }
    var time = typeIsObj(t) ? t : gettime(t);
    return time.month + '' + time.day + '';
}

function six(t) {
    if(!t){
        return ''
    }
    var time = typeIsObj(t) ? t : gettime(t);
    return time.hour + ':' + time.minute + ':' + time.second;
}

function severn(t) {
    if(!t){
        return ''
    }
    var time = typeIsObj(t) ? t : gettime(t);
    return time.week
}

function typeIsObj(t){
   return Object.prototype.toString.call(t) == '[object Object]' ?  true : false
}


function getNowFormatDate (obj) {
    var date = new Date(obj);
    var seperator1 = "/";
    var seperator2 = ":";
    var month = date.getMonth() + 1;
    var strDate = date.getDate();
    if (month >= 1 && month <= 9) {
      month = "0" + month;
    }
    if (strDate >= 0 && strDate <= 9) {
      strDate = "0" + strDate;
    }
    var minuts =
      date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
    var currentdate =
      date.getFullYear() +
      seperator1 +
      month +
      seperator1 +
      strDate +
      " " +
      date.getHours() +
      seperator2 +
      minuts +
      seperator2 +
      date.getSeconds();
    return currentdate;
  }
export default {
    gettime:gettime,//obj
    one: one, //2009-09-09 
    two: two, //2009-09-09 08h
    three: three,//2009-09-09 08:02
    four: four,//2009-09-09 08:02:02
    five: five,//66
    six:six,//10:10:10;
    severn:severn,
    changeTimeLength:changeTimeLength,
    getNowFormatDate: getNowFormatDate
};



