import httpserver from "../../utils/http.js";
import api from "../../utils/api.js";
export default {
  data() {
    return {
      serialPort: "",
      port: "",
      baudRate: '',
      dataBit: "",
      checkBit: "",
      stopBit: ''
    };
  },
  created () {
    let serial = JSON.parse(window.localStorage.getItem("serialPort"))  
    console.log(serial);
    this.port=serial.port;
    this.baudRate=serial.baudRate;
    this.dataBit=serial.dataBit;
    this.checkBit=serial.checkBit;
    this.stopBit=serial.stopBit;
  },
  methods: {
    save() {
      let body = {
        port: this.port,
        baudRate: this.baudRate,
        dataBit: this.dataBit,
        checkBit: this.checkBit,
        stopBit: this.stopBit,
        terminalCode: JSON.parse(window.localStorage.getItem("terminal"))
          .terminalCode
      };
      httpserver(api.saveSystemCom, body).then(response => {
        console.log(response.data.returnCode);
        if (response.data.returnCode == "0") {
          this.$message({
            message: "",
            type: "success"
          });
          window.localStorage.setItem("serialPort", JSON.stringify(body));
        }
      });
    }
  }
};



