import appFoot from "./foot.vue";
import appBody from "./body.vue";
import appHead from "./head.vue";
export default {
  data() {
    return {
      isShowNavMenu: false
    };
  },
  created() {},
  components: {
    appFoot,
    appBody,
    appHead
  },
  methods: {
    closeMenu() {
      this.isShowNavMenu = false;
    },
    NavMenuEmit(data) {
      this.isShowNavMenu = !this.isShowNavMenu;
    }
  }
};




