import httpserver from "../../utils/http.js";
import api from "../../utils/api.js";
import getNowFormatDate from "../../utils/timeFormat.js";
export default {
  beforeRouteLeave(to, from, next) {
    this.closeCom();
    next();
  },
  created() {
    this.workstations();
    this.productionstores();
  },
  mounted() {
    this.openCom();
  },
  data() {
    return {
      radio3: "",
      arr: [],
      code: "",
      scanCode: "",
      tableData: [],
      editFlag: false,
      batchNum: "",
      stockNum: "",
      id: ""
    };
  },
  methods: {
    show: function(ev) {
      let _this = this;
      if (ev.keyCode == 13) {
        this.productionstore();
      }
    },
    workstations() {
      let loc = JSON.parse(window.localStorage.getItem("terminal"));
      let body = {
        terminalCode: loc.terminalCode
      };
      httpserver(api.workstations, body).then(res => {
        console.log(res);
        this.arr = res.data.data;
      });
    },
    get(code) {
      this.code = code;
      this.productionstores();
    },
    productionstore() {
      let loc = JSON.parse(window.localStorage.getItem("terminal"));
      //workstationCode
      let station;
      if (this.code == "") {
        station = loc.workStationCode;
      } else {
        station = this.code;
      }
      let body = {
        workStationCode: station,
        scanCode: this.scanCode
      };
      httpserver(api.productionstore, body).then(res => {
        console.log(res);
        if (res.data.returnCode == "0") {
          this.productionstores();
          this.$message({
            type: "success",
            message: res.data.data
          });
        }
      });
    },
    productionstores() {
      let loc = JSON.parse(window.localStorage.getItem("terminal"));
      let station;
      if (this.code == "") {
        station = loc.workStationCode;
      } else {
        station = this.code;
      }
      let body = {
        workStationCode: station
      };
      httpserver(api.productionstores, body).then(res => {
        console.log(res);
        this.tableData = res.data.data;
      });
    },
    //
    dateFormat: function(row, column) {
      var date = row[column.property];
      if (date == undefined) {
        return "";
      }
      return getNowFormatDate.getNowFormatDate(date);
    },
    handleEdit(index, row) {
      console.log(row);
      console.log(index);
      this.editFlag = true;
      this.stockNum = row.stockNum;
      this.batchNum = row.batchNum;
      this.id = row.id;
    },
    handleSave() {
      this.editFlag = false;
      let body = {
        id: this.id,
        batchNum: this.batchNum,
        stockNum: this.stockNum
      };
      httpserver(api.productionstore2, body).then(res => {
        console.log(res);
        this.productionstores();
      });
    },
    handleDelete(index, row) {
      let id = row.id;
      let body = {
        id: id,
        deleteFlag: 1
      };
      httpserver(api.productionstore2, body).then(res => {
        console.log(res);
        this.productionstores();
      });
    },
    openCom() {
      try {
        let _this = this;
        let port = new SerialPort(
          JSON.parse(window.localStorage.getItem("serialPort")).port, { autoOpen: false }
        );
        let Readline = SerialPort.parsers.Readline;
        let parser = new Readline();
        port.pipe(parser);
        port.open(function(error) {
          if (error) {
            console.log(error);
            _this.$message({
              message: "",
              type: "error"
            });
            return console.log("Error opening port:", error);
          } else {
            _this.$message({
              message: "",
              type: "success"
            });
          }
        });
        parser.on("data", function(data) {
          if (data.length <= 2) {
            _this.$message({
              type: "error",
              message: "!"
            });
          } else {
            _this.scanCode = data;
            _this.productionstore();
          }
        });
        _this.serialPort = port;
      } catch (err) {
        //console.log(err);
      }
    },
    closeCom() {
      try {
        if (this.serialPort.isOpen) {
          let _this = this;
          _this.serialPort.close(function(err) {
            if (err) {
              console.log(err);
              _this.$message({
                message: "",
                type: "error"
              });
            } else {
              _this.$message({
                message: "",
                type: "success"
              });
            }
          });
        }
      } catch (err) {
        // console.log(err);
      }
    }
  }
};




