import httpserver from "../../utils/http.js";
import api from "../../utils/api.js";
import getNowFormatDate from "../../utils/timeFormat.js";

export default {
  data() {
    return {
      centerDialogVisible: false,
      productionOrderNum: "",
      tableData: [],
      gridData: [],
      proinfo: {},
      plannedQty: 0,
      code: "",
      pagesize: 8,
      currentPage: 1,
      total: 0,
      productCount: 0,
      dialogTableVisible: false,
      currentRow: null,
      count: 0,
      serialPort: "",
      total: 0
    };
  },
  beforeRouteLeave(to, from, next) {
    this.closeCom();
    next();
  },
  created() {
    this.getMachiningProductionQueue();
  },
  mounted() {
    this.openCom();
  },
  methods: {
    show: function(ev) {
      let _this = this;
      if (ev.keyCode == 13) {
        this.getCount();
      }
    },
    show2: function(ev) {
      if (ev.keyCode == 13) {
        this.add();
      }
    },
    add() {
      if (this.productionOrderNum == "") {
        this.$message({
          type: "error",
          message: ""
        });
      } else if (this.code == "") {
        this.$message({
          type: "error",
          message: ""
        });
      } else if (
        this.productCount > 0 &&
        this.productCount + this.count > this.plannedQty
      ) {
        this.centerDialogVisible = true;
      } else {
        this.validMachiningProductRecord();
      }
    },
    getCount() {
      let body = {
        materialCode: this.code
      };

      httpserver(api.getAmount, body).then(res => {
        this.count = res.data.data;
        //  this.scanCount++;
      });
    },
    dateFormat: function(row, column) {
      var date = row[column.property];
      if (date == undefined) {
        return "";
      }
      return getNowFormatDate.getNowFormatDate(date);
    },
    openCom() {
      try {
        let _this = this;
        let baudRate = JSON.parse(window.localStorage.getItem("serialPort"))
          .baudRate;
        console.log(baudRate);
        let port = new SerialPort(
          JSON.parse(window.localStorage.getItem("serialPort")).port,
          {
            baudRate: parseInt(baudRate),
            autoOpen: false
          },
          false
        );
        console.log(port);
        let Readline = SerialPort.parsers.Readline;
        let parser = new Readline();
        port.pipe(parser);
        port.open(function(error) {
          if (error) {
            console.log(error);
            _this.$message({
              message: "",
              type: "error"
            });
            return console.log("Error opening port:", error);
          } else {
            _this.$message({
              message: "",
              type: "success"
            });
          }
        });
        parser.on("data", function(data) {
         if (data.length<=2) {
            _this.$message({
              type: "error",
              message: "!"
            });
          } else {
            _this.code = data;
            _this.getCount();
          }
        });
        _this.serialPort = port;
      } catch (err) {
        //console.log(err);
      }
    },
    closeCom() {
      try {
        if (this.serialPort.isOpen) {
          let _this = this;
          _this.serialPort.close(function(err) {
            if (err) {
              console.log(err);
              _this.$message({
                message: "",
                type: "error"
              });
            } else {
              _this.$message({
                message: "",
                type: "success"
              });
            }
          });
        }
      } catch (err) {
        // console.log(err);
      }
    },
    setCurrent(row) {
      this.$refs.table.setCurrentRow(row);
    },
    continueON() {
      this.centerDialogVisible = false;
      this.validMachiningProductRecord();
    },
    onFinish() {
      this.centerDialogVisible = false;
      this.updateMachiningProduction();
      this.empty();
    },
    empty() {
      this.code = "";
      this.count = 0;
      this.productCount = 0;
      this.proinfo = "";
      // this.scanCount=0;
    },
    updateMachiningProduction() {
      let body = {
        productionOrderNum: this.productionOrderNum
      };
      httpserver(api.updateMachiningProduction, body).then(res => {
        if (res.data.returnCode == "0") {
          this.$message({
            type: "success",
            message: ""
          });
          this.getMachiningProductionQueue();
        }
      });
    },
    validMachiningProductRecord() {
      let loc = JSON.parse(window.localStorage.getItem("terminal"));
      if (this.count > 0 && /(^[1-9]\d*$)/.test(this.count)) {
        let body = {
          workStationCode: loc.workStationCode,
          scanCode: this.code,
          productionOrderNum: this.productionOrderNum,
          qty: this.count
        };
        console.log(this.count);
        httpserver(api.validMachiningProductRecord, body).then(response => {
          console.log(response);
          if (response.data.returnCode == "0") {
            // this.productCount=this.productCount+this.count;
            this.getMachiningProductionQueue();
          }
        });
      } else {
        this.$message({
          type: "error",
          message: ""
        });
      }
    },
    getHistoryInfo() {
      this.dialogTableVisible = true;
      let loc = JSON.parse(window.localStorage.getItem("terminal"));
      let body = {
        workStationCode: loc.workStationCode,
        pageNo: this.currentPage,
        pageSize: this.pagesize,
        productionOrderNum: this.productionOrderNum
      };
      httpserver(api.getMachingHisRecords, body).then(response => {
        let resData = response.data.data;
        this.total = resData.toalCount;
        this.gridData = resData.list;
      });
    },
    getMachiningProductionQueue: function() {
      let loc = JSON.parse(window.localStorage.getItem("terminal"));
      let body = {
        workCenterCode: loc.workCenterCode,
        endRow: 4
      };
      httpserver(api.getMachiningProductionQueue, body).then(res => {
        this.tableData = res.data.data;
      });
    },
    current(val, old) {
      if (val != null) {
        this.currentRow = val;
        this.proinfo = val;
        this.productCount = this.currentRow.scanQty;
        this.plannedQty = this.currentRow.plannedQty;
        this.productionOrderNum = this.currentRow.productionOrderNum;
      } else if (this.tableData != null) {
        this.tableData.forEach((value, index, table) => {
          if (table[index].productionOrderNum == old.productionOrderNum) {
            this.setCurrent(this.tableData[index]);
          }
        });
      }
    },
    //      
    handleSizeChange(val) {
      console.log(` ${val} `);
    },
    //      
    handleCurrentChange(val) {}
  }
};



