import httpserver from "../../utils/http.js";
import api from "../../utils/api.js";
import getNowFormatDate from "../../utils/timeFormat.js";
export default {
  data() {
    return {
      centerDialogVisible: false,
      productionOrderNum: "",
      tableData: [],
      gridData: [],
      proinfo: {},
      plannedQty: 0,
      code: "",
      productCount: 0,
      dialogTableVisible: false,
      currentRow: null,
      currentPage: 1,
      serialPort: "",
      total: 0,
      pagesize: 3
    };
  },
  beforeRouteLeave(to, from, next) {
    this.closeCom();
    next();
  },
  created() {
    this.getMachiningProductionQueue();
  },
  mounted() {
    this.openCom();
  },
  methods: {
    show: function(ev) {
      let _this = this;
      if (ev.keyCode == 13) {
        this.validMachiningProductRecord();
      }
    },
    openCom() {
      try {
        let _this = this;
        let baudRate = JSON.parse(window.localStorage.getItem("serialPort"))
          .baudRate;
        console.log(baudRate);
        let port = new SerialPort(
          JSON.parse(window.localStorage.getItem("serialPort")).port,
          {
            baudRate: parseInt(baudRate),
            autoOpen: false
          },
          false
        );
        console.log(port);
        let Readline = SerialPort.parsers.Readline;
        let parser = new Readline();
        port.pipe(parser);
        port.open(function(error) {
          if (error) {
            console.log(error);
            _this.$message({
              message: "",
              type: "error"
            });
            return console.log("Error opening port:", error);
          } else {
            _this.$message({
              message: "",
              type: "success"
            });
          }
        });
        parser.on("data", function(data) {
          console.log(data);
          console.log(typeof data);
          console.log(data.length);
          if (data.length <= 2) {
            _this.$message({
              type: "error",
              message: "!"
            });
          } else {
            _this.code = data;
            _this.validMachiningProductRecord();
          }
        });
        _this.serialPort = port;
      } catch (err) {
        //console.log(err);
      }
    },
    closeCom() {
      try {
        if (this.serialPort.isOpen) {
          let _this = this;
          _this.serialPort.close(function(err) {
            if (err) {
              console.log(err);
              _this.$message({
                message: "",
                type: "error"
              });
            } else {
              _this.$message({
                message: "",
                type: "success"
              });
            }
          });
        }
      } catch (err) {
        // console.log(err);
      }
    },
    setCurrent(row) {
      this.$refs.table.setCurrentRow(row);
    },
    //    validplannedQty() {
    //      if (this.productCount < this.plannedQty) {
    //        this.validMachiningProductRecord();
    //      } else if(this.productCount >= this.plannedQty&&this.productCount>0) {
    //        this.centerDialogVisible = true;
    //      }
    //    },
    continueON() {
      this.validMachiningProductRecord();
      this.centerDialogVisible = false;
    },
    finish() {},
    onFinish() {
      this.centerDialogVisible = false;

      let body = {
        productionOrderNum: this.productionOrderNum
      };
      this.$confirm("")
        .then(_ => {
          httpserver(api.updateMachiningProduction, body).then(res => {
            this.productCount = 0;
            this.plannedQty = 0;
            if (res.data.returnCode == "0") {
              this.getMachiningProductionQueue();
              this.proinfo = {};
              this.code = "";
            }
          });
        })
        .catch(_ => {});
    },
    validMachiningProductRecord() {
      let loc = JSON.parse(window.localStorage.getItem("terminal"));
      let code = this.code.replace(/\r/g, "");
      this.code = code.replace(/\n/g, "");
      let body = {
        workStationCode: loc.workStationCode,
        scanCode: this.code,
        productionOrderNum: this.productionOrderNum
      };
      httpserver(api.validMachiningProductRecord, body).then(response => {
        if (response.data.returnCode == "0") {
          this.productCount++;
          if (this.productCount >= this.plannedQty && this.productCount > 0) {
            this.centerDialogVisible = true;
          }
          this.getMachiningProductionQueue();
        }
      });
    },
    dateFormat: function(row, column) {
      var date = row[column.property];
      if (date == undefined) {
        return "";
      }
      return getNowFormatDate.getNowFormatDate(date);
    },
    getHistoryInfo() {
      this.dialogTableVisible = true;
      let loc = JSON.parse(window.localStorage.getItem("terminal"));
      let body = {
        workStationCode: loc.workStationCode,
        pageNo: this.currentPage,
        pageSize: this.pagesize,
        productionOrderNum: this.productionOrderNum
      };
      if (this.productionOrderNum == "") {
        this.$message({
          type: "error",
          message: ""
        });
      } else {
        httpserver(api.getMachingHisRecords, body).then(response => {
          let resData = response.data.data;
          this.gridData = resData.list;
          this.total = resData.toalCount;
        });
      }
    },
    getMachiningProductionQueue: function() {
      let loc = JSON.parse(window.localStorage.getItem("terminal"));
      let body = {
        workCenterCode: loc.workCenterCode,
        endRow: 4
      };
      httpserver(api.getMachiningProductionQueue, body).then(res => {
        this.tableData = res.data.data;
      });
    },
    current(val, old) {
      if (val != null) {
        this.currentRow = val;
        this.proinfo = val;
        this.productCount = this.currentRow.scanQty;
        this.plannedQty = this.currentRow.plannedQty;
        this.productionOrderNum = this.currentRow.productionOrderNum;
      } else if (this.tableData != null) {
        this.tableData.forEach((value, index, table) => {
          if (table[index].productionOrderNum == old.productionOrderNum) {
            this.setCurrent(this.tableData[index]);
          }
        });
      }
    },
    handleSizeChange(val) {},
    //      
    currentChange(val) {
      this.currentPage = val;
      let loc = JSON.parse(window.localStorage.getItem("terminal"));
      let body = {
        workStationCode: loc.workStationCode,
        pageNo: this.currentPage,
        pageSize: this.pagesize
      };
      httpserver(api.getMachingHisRecords, body).then(response => {
        let resData = response.data.data;
        this.gridData = resData.list;
        this.total = resData.toalCount;
      });
    }
  }
};



