import httpserver from "../../utils/http.js";
import api from "../../utils/api.js";

export default {
  data() {
    return {
      currentRow: "",
      productionOrderNum: "",
      tableData: [],
      motorData: [],
      gridData: [],
      code: "",
      productNo: "", //
      productCount: 0,
      dialogTableVisible: false,
      currentRow: null,
      proinfo: {},
      materialCode: "",
      scantype: "produce"
    };
  },
  beforeRouteLeave(to, from, next) {
    this.closeCom();
    next();
  },
  created() {},
  mounted() {
    this.openCom();
  },
  methods: {
    show: function(ev) {
      if (ev.keyCode == 13) {
        this.getOrderInfo();
      }
    },
    show2: function(ev) {
      if (ev.keyCode == 13) {
        this.getMaterialByCode();
      }
    },
    //      
    getOrderInfo() {
      if (this.code != "") {
        let body = {
          serialNo: this.code
        };
        httpserver(api.getSerialNoInformation, body).then(res => {
          this.proinfo = res.data.data;
          if (this.proinfo == null) {
            this.proinfo = {};
          }
        });
      } else {
        this.$message({
          message: "",
          type: "error"
        });
      }
    },
    //      
    getMaterialByCode() {
      if (this.materialCode !== "") {
        let body = {
          materialCode: this.materialCode
        };
        httpserver(api.getMaterialByCode, body).then(res => {
          var resData = res.data.data;
          console.log(resData);
          if (resData != null) {
            this.motorData.push(resData);
          }
        });
      } else {
        this.$message({
          message: "",
          type: "error"
        });
      }
    },
    //
    saveHistoryInfo() {
      let motorObj = {};
      let motorArr = [];

      for (var i = 0; i < this.motorData.length; i++) {
        let infoData = this.motorData[i];
        motorObj.materialCode = infoData.materialCode;
        motorObj.qty = 1;
        motorObj.workCenterCode = JSON.parse(
          localStorage.getItem("terminal")
        ).workCenterCode;
        motorObj.workStationCode = JSON.parse(
          localStorage.getItem("terminal")
        ).workStationCode;
        motorObj.serialNo = this.code;
        motorObj.scanCode = this.materialCode;
        motorArr.push(motorObj);
      }
      let body = {
        list: motorArr
      };
      httpserver(api.completeProductionOrder, body).then(res => {
        if (res.data.returnCode == "0") {
          this.$message({
            message: res.data.data,
            type: "success"
          });
        }
        this.code = "";
        this.materialCode = "";
        this.motorData = [];
        this.proinfo = {};
      });
    },
    getHistoryInfo() {
      this.dialogTableVisible = true;
      this.dialogTableVisible = true;
      let loc = JSON.parse(window.localStorage.getItem("terminal"));
      let body = {
        workStationCode: loc.workStationCode,
        pageNo: "1",
        pageSize: "1"
      };
      httpserver(api.getHistoryInfo, body).then(response => {
        console.log(response);
        let resData = response.data.data;
        this.gridData = resData.productionStnRecords;
      });
    },
    changeType1() {
      console.log(1);
      this.scantype = "produce";
    },
    changeType2() {
      console.log(2);
      this.scantype = "material";
    },
    openCom() {
      try {
        let _this = this;
        let baudRate = JSON.parse(window.localStorage.getItem("serialPort"))
          .baudRate;
        console.log(baudRate);
        let port = new SerialPort(
          JSON.parse(window.localStorage.getItem("serialPort")).port,
          {
            baudRate: parseInt(baudRate),
            autoOpen: false
          },
          false
        );
        console.log(port);
        let Readline = SerialPort.parsers.Readline;
        let parser = new Readline();
        port.pipe(parser);
        port.open(function(error) {
          if (error) {
            console.log(error);
            _this.$message({
              message: "",
              type: "error"
            });
            return console.log("Error opening port:", error);
          } else {
            _this.$message({
              message: "",
              type: "success"
            });
          }
        });
        parser.on("data", function(data) {
         if (data.length<=2) {
            _this.$message({
              type: "error",
              message: "!"
            });
          } else {
            if (_this.scantype == "produce") {
              _this.code = data;
              _this.getOrderInfo();
            } else {
              _this.materialCode = data;
              _this.getMaterialByCode();
            }
          }
        });
        _this.serialPort = port;
      } catch (err) {
        //console.log(err);
      }
    },
    closeCom() {
      try {
        if (this.serialPort.isOpen) {
          let _this = this;
          _this.serialPort.close(function(err) {
            if (err) {
              console.log(err);
              _this.$message({
                message: "",
                type: "error"
              });
            } else {
              _this.$message({
                message: "",
                type: "success"
              });
            }
          });
        }
      } catch (err) {
        // console.log(err);
      }
    }
  }
};



