import util from "../../../utils/util.js";
// import config from '../../../js/config';
import httpserver from "../../../utils/http.js";
import api from "../../../utils/api.js";

export default {
  name: "navlist",
  props: ["isMenuShow"],
  created() {
    this.userinfo();
    this.MenuListData();
  },
  data() {
    return {
      showColor: true,
      user: window.localStorage.getItem("userCode")
    };
  },
  methods: {
    ddd() {},
    quit() {
      this.$router.push("/login");
      httpserver(api.logout).then(res => {
        util.sessionClean();
        clearInterval(this.$store.state.timer1);
        
      });
    },
    userinfo() {
      this.userName = sessionStorage.getItem("userCode");
    },
    MenuListData() {
      let obj = JSON.parse(localStorage.getItem("list"));
      this.MenuListData = obj;
      // console.log(obj);
      //        var title_ul=document.getElementsByClassName('title_list');
      //        title_ul.style.height=obj.length*42+'px'
      // return obj
    },
    getTitle(index) {
      let getMenuText = this.$refs.menuTitle[index].innerText;
      this.$store.state.title = getMenuText;
      localStorage.setItem("title", getMenuText);
    }
  }
};



