import { mapGetters, mapActions } from "vuex";
import httpserver from "../../utils/http.js";
import api from "../../utils/api.js";
import util from "../../utils/util.js";
export default {
  data() {
    return {
      options: [
        {
          value: "zh",
          label: ""
        },
        {
          value: "en",
          label: "English"
        }
      ],
      value: "",
      workStationText: JSON.parse(window.localStorage.getItem("terminal"))
        .workStationText,
      station: "",
      user: window.localStorage.getItem("userCode")
    };
  },
  watch: {},
  computed: {
    currentRoute() {
      return this.$route.path;
    },
    title() {
      let route = this.$route.path;
      let title = "";
      switch (route) {
        case "/system":
          title = "";
          return title;
          break;
        case "/initialize":
          title = "";
          return title;
          break;
        case "/online/assemblyOnline":
          title = "";
          return title;
          break;
        case "/online/machineplusOnline":
          title = "";
          return title;
          break;
        case "/assembleInsert":
          title = "";
          return title;
          break;
        case "/hothost":
          title = "";
          return title;
          break;
        case "/MachineInsert":
          title = "";
          return title;
          break;
        case "/online/crankshaftOnline":
          title = "";
          return title;
          break;
        case "/home":
          title = "";
          return title;
          break;
        case "/menu":
          title = "";

          return title;
          break;
        case "/machineRepair":
          title = "";
          return title;
          break;
        case "/connectRod":
          title = "";
          return title;
          break;
        // case "/concentratRepair":
        //   if (this.$i18n.locale == "cn") {
        //     if (this.$store.state.titles == "") {
        //       title = "";
        //     } else {
        //       title = "";
        //     }
        //   } else {
        //     title = "machineRepair";
        //   }
        //   return title;
        //   break;
        case "/auxMachine":
          title = "";
          return title;
          break;
        case "/cyheadOnline":
          title = "";
          return title;
          break;
        case "/NotFound":
          title = "error";
          return title;
          break;
        case "/insideAssemble":
          title = "";
          return title;
          break;
      }
    }
  },
  created() {
    this.change();
  },
  methods: {
    logout() {
      this.$router.push("/login");
      httpserver(api.logout).then(res => {
        util.sessionClean();
        clearInterval(this.$store.state.timer1);
      });
    },
    change() {
      console.log(this.$store.state.titles);
      if (this.value == "zh") {
        this.$i18n.locale = "cn";
      } else if (this.value == "en") {
        this.$i18n.locale = "en";
      }
    }
  }
};



