import httpserver from "../../utils/http.js";
import api from "../../utils/api.js";
import mqttLib from "../../utils/mqtt.js";

export default {
  data() {
    return {
      StepDatas: [
        { title: ",...", status: "wait" },
        { title: "...", status: "wait" },
        { title: ",...", status: "wait" },
        { title: "!", status: "wait" },
        { title: "!", status: "wait" }
      ],
      serialPort: "",
      name: "pro-gress",
      number: 0,
      dialogTableVisible: false,
      tableData: [],
      total: 0,
      gridData: [],
      proinfo:'',
      code: ""
    };
  },
 beforeRouteLeave(to, from, next) {
    this.closeCom();
    next();
  },
  created() {
    this.subscribe();
    console.log("");
  },

  beforeDestroy() {
    this.unsubscribe();
    console.log("");
  },
    mounted() {
    this.openCom();
  },
  methods: {
    show: function(ev) {
      let _this = this;
      if (ev.keyCode == 13) {
        this.getSerialNoInformation();
      }
    },
     openCom() {
      try {
        let _this = this;
       let baudRate=JSON.parse(window.localStorage.getItem("serialPort")).baudRate;
        console.log(baudRate);
        let port = new SerialPort(
          JSON.parse(window.localStorage.getItem("serialPort")).port,
          {
            baudRate: parseInt(baudRate),
            autoOpen:false
          },
          false
        );
        console.log(port);
        let Readline = SerialPort.parsers.Readline;
        let parser = new Readline();
        port.pipe(parser);
        port.open(function(error) {
          if (error) {
            console.log(error);
            _this.$message({
              message: "",
              type: "error"
            });
            return console.log("Error opening port:", error);
          } else {
            _this.$message({
              message: "",
              type: "success"
            });
          }
        });
        parser.on("data", function(data) {
          if (data.length<=2) {
            _this.$message({
              type: "error",
              message: "!"
            });
          } else {
             _this.code = data;
            _this.getSerialNoInformation();
          }
         
        });
        _this.serialPort = port;
      } catch (err) {
        //console.log(err);
      }
    },
    closeCom() {
      try {
        if (this.serialPort.isOpen) {
          let _this = this;
          _this.serialPort.close(function(err) {
            if (err) {
              console.log(err);
              _this.$message({
                message: "",
                type: "error"
              });
            } else {
              _this.$message({
                message: "",
                type: "success"
              });
            }
          });
        }
      } catch (err) {
        // console.log(err);
      }
    },
    subscribe() {
      let _this = this;
      let loc = JSON.parse(window.localStorage.getItem("terminal"));
      let workStationCode = loc.workStationCode;
      // let topic = "/logs/B01";
      let topic = "/logs/" + workStationCode;
      mqttLib.subscribe(topic, "message");
      mqttLib.registerMessageHandler(topic, "message", function(message) {
        console.log(message.payloadString);
        let data = JSON.parse(message.payloadString);
        let type = data.Type;

        if (type == "UNLOAD") {
          let step = JSON.parse(message.payloadString).Content.Step;
          let log = JSON.parse(message.payloadString).Content.Log;
          switch (step) {
            case "1":
              _this.number = 1;
              _this.StepDatas.forEach(function(value, index, array) {
                array[index].status = "wait";
              });
              _this.StepDatas[0].status = "success";
              _this.StepDatas[0].title = log;
              break;
            case "2":
              _this.number = 2;
              _this.StepDatas[1].status = "success";
              _this.StepDatas[1].title = log;
              break;
            case "3":
              _this.number = 3;
              _this.StepDatas[2].status = "success";
              _this.StepDatas[2].title = log;
              break;
            case "4":
              _this.number = 4;
              _this.StepDatas[3].status = "success";
              _this.StepDatas[3].title = log;
              break;
            case "5":
              _this.number = 5;
              _this.StepDatas[4].status = "success";
              _this.StepDatas[4].title = log;

              break;
            default:
              _this.StepDatas[_this.number].title = log;
              _this.StepDatas[_this.number].status = "error";
          }
        } else {
          _this.code = JSON.parse(message.payloadString).Content.Log;
          _this.getSerialNoInformation2();
        }
      });
    },
    unsubscribe() {
     let loc = JSON.parse(window.localStorage.getItem("terminal"));
      let workStationCode = loc.workStationCode;
      // let topic = "/logs/B01";
      let topic = "/logs/" + workStationCode;
      mqttLib.unsubscribe(topic, "message");
    },
    getHistoryInfo() {
      this.dialogTableVisible = true;
      let loc = JSON.parse(window.localStorage.getItem("terminal"));
      let body = {
        workStationCode: loc.workStationCode,
        pageNo: "1",
        pageSize: "1"
      };
      httpserver(api.getHistoryInfo, body).then(response => {
        let resData = response.data.data;
        this.gridData = resData.productionStnRecords;
        this.total = resData.toalCount;
      });
    },
    //      
    handleSizeChange(val) {},
    //      
    handleCurrentChange(val) {
      this.dialogTableVisible = true;
      let loc = JSON.parse(window.localStorage.getItem("terminal"));
      let body = {
        workStationCode: loc.workStationCode,
        pageNo: val,
        pageSize: "1"
      };
      httpserver(api.getHistoryInfo, body).then(response => {
        let resData = response.data.data;
        this.gridData = resData.productionStnRecords;
        this.total = resData.toalCount;
      });
    },
    getSerialNoInformation() {
      let body = {
        serialNo: this.code
      };
      httpserver(api.getSerialNoInformation, body).then(res => {
        //6947463266069
        if (res.data.returnCode == "0") {
          this.proinfo = res.data.data;
          this.productCount++;
          if (res.data.data.hotTest == "1") {
            console.log(
              (document.getElementById("ishotTest").style.display = "flex")
            );
          } else {
            document.getElementById("ishotTest").style.display = "none";
            this.productionStatusChange();
          }
        }
      });
    },
    getSerialNoInformation2() {
      let body = {
        serialNo: this.code
      };
      httpserver(api.getSerialNoInformation, body).then(res => {
        //6947463266069
        if (res.data.returnCode == "0") {
          this.proinfo = res.data.data;
          this.productCount++;
          if (res.data.data.hotTest == "1") {
            console.log(
              (document.getElementById("ishotTest").style.display = "flex")
            );
          } else {
            document.getElementById("ishotTest").style.display = "none";
          }
        }
      });
    },
    productionStatusChange() {
      let loc = JSON.parse(window.localStorage.getItem("terminal"));
      let body = {
        serialNo: this.code,
        workStationCode: loc.workStationCode
      };
      httpserver(api.productionStatusChange, body).then(res => {
        console.log(res);
        if (res.data.returnCode == "0") {
          this.$message({
            type: "success",
            message: res.data.data
          });
        }
      });
    }
  }
};



